<template>
  <div>
    <Mnavbar @select="handleSelect"></Mnavbar>
    <div v-show="history">
      <el-select v-model="historybill" placeholder="" @change="handlebillselect">
         <el-option
           v-for="item in historybills"
           :key="item.date"
           :label="item.label"
           :value="item.date">
         </el-option>
       </el-select>
    </div>
    <div v-show="addnew">
    <el-form label-width="70px" :model="house" label-position="left">
    <div v-for="(item,index) in eletricmonitors" v-show="house.eletric">
      <h4 class="info-title">{{''+index}}</h4>
      <div class="info-wrap">
        <div class="last-record-row">{{` ${item.lastrecord}   ${item.lastdate}` }}</div>
        <el-form-item label="">
          <el-input v-model="item.current"></el-input>
        </el-form-item>
      </div>
    </div>
    <div v-for="(item,index) in watermonitors" v-show="house.water">
      <h4 class="info-title">{{''+index}}</h4>
      <div class="info-wrap">
        <div class="last-record-row">{{` ${item.lastrecord}   ${item.lastdate}` }}</div>
        <el-form-item label="">
          <el-input v-model="item.current"></el-input>
        </el-form-item>
      </div>
    </div>
    </el-form>
    <el-button type="primary" round @click="generate"></el-button>
    <div v-show="billon">
      <div v-show="house.eletric">
        <h5 class="bill-title"></h5>
        <el-table v-for="(item,index) in bill.eletric" :data="item">
          <el-table-column
          prop="current"
          label="">
          </el-table-column>
          <el-table-column
          prop="lastrecord"
          label="">
          </el-table-column>
          <el-table-column
          prop="usage"
          label="">
          </el-table-column>
          <el-table-column
          prop="price"
          label="">
          </el-table-column>
          <el-table-column
          prop="total"
          label="">
          </el-table-column>
        </el-table>
      </div>
      <div v-show="house.water">
        <h5 class="bill-title"></h5>
        <el-table v-for="(item,index) in bill.water" :data="item">
          <el-table-column
          prop="current"
          label="">
          </el-table-column>
          <el-table-column
          prop="lastrecord"
          label="">
          </el-table-column>
          <el-table-column
          prop="usage"
          label="">
          </el-table-column>
          <el-table-column
          prop="price"
          label="">
          </el-table-column>
          <el-table-column
          prop="total"
          label="">
          </el-table-column>
        </el-table>
      </div>
      <el-table show-summary :data="bill.chargeitems">
        <el-table-column
        prop="name"
        label="">
        </el-table-column>
        <el-table-column
        prop="price"
        label="">
        </el-table-column>
      </el-table>
      <div class="link-btn-wrap"><a class="link-button" :href="'mprint://myprint.ciwi/easyhome?'+print.substring(1)"></a></div>
    </div>
  </div>
  </div>
</template>

<script>
import Utils from '../utils.js'
import Mnavbar from './Mnavbar.vue'
export default {
  name: 'generatebill',
  components: {
    Mnavbar: Mnavbar,
  },
  data () {
    return {
      house: {},
      eletricmonitors: [],
      watermonitors: [],
      chargeitems: {},
      print: '',
      billon: false,
      addnew: true,
      history: false,
      active: 'addnew',
      historybills: [],
      historybill: '',
    };
  },
  beforeRouteEnter (to,from,next) {
    next((vm) => {

    })
  },
  computed: {
    bill () {
      const that = this;
      const tempbill = {
        eletric: [],
        water: [],
        chargeitems: [
          {
            name: '',
            price: this.house.price?this.house.price:0,
         },
         {
           name: '',
           price: 0,
         },
         {
           name: '',
           price: 0,
         }
        ],
      };
      if(this.eletricmonitors.length > 0){
      this.eletricmonitors.forEach((e) => {
        e.usage = e.current - e.lastrecord;
        e.total = (e.usage * e.price).toFixed(2);
        tempbill.chargeitems[1].price += parseFloat(e.total);
      });
      tempbill.eletric = this.eletricmonitors.map((e) => {
        let k = [e];
        return k;
      });
    }
      if(this.watermonitors.length > 0){
      this.watermonitors.forEach((e) => {
        e.usage = e.current - e.lastrecord;
        e.total = (e.usage * e.price).toFixed(2);
        tempbill.chargeitems[2].price += parseFloat(e.total);
      });
      tempbill.water = this.watermonitors.map((e) => {
        let k = [e];
        return k;
      });
    }
      this.house.chargeitems&&(this.house.chargeitems.forEach((e) => {
        tempbill.chargeitems.push(this.chargeitems[e]);
      }));
    return tempbill;
    },
  },
  created () {
    const that = this;
    let p0 = this.getChargeitems();
    let p = new Promise((resolve, reject) => {
      window.firebase.database().ref('houses/' + that.$route.params.id).once('value').then((dataSnapshot) => {
        that.house = dataSnapshot.val();
        console.log(that.house);
        resolve();
    })
  });
     Promise.all([p, p0]).then(() => {
       that.getMonitors();
     });
  },
  methods: {
    getMonitors () {
      if(this.house.eletric) {
        this.getRecorddata('eletric');
      }
      if(this.house.water) {
        this.getRecorddata('water');
      }
    },
    getChargeitems () {
      const that = this;
       let p = new Promise((resolve, reject) => {
        window.firebase.database().ref('chargeitems').once('value', (dataSnapshot) => {
           that.chargeitems = dataSnapshot.val();
           resolve()
         });
       });
      return p;
    },
    getRecorddata (type) {
      const that = this;
      const tables= {
        eletric: {
          tname: 'eletricmonitors',
          hmonitors: this.house.eletricmonitors,
        },
        water: {
          tname: 'watermonitors',
          hmonitors: this.house.watermonitors,
        },
      };
      const temparray = [];
      const ts = Date.now();
      let dataPromises = tables[type].hmonitors.map((e) => {
        let getdata = new Promise((resolve0, reject) => {
          let obj = {
            current: '',
          };
          let p1 = new Promise((resolve1,reject) => {
            window.firebase.database().ref(tables[type].tname + '/' + e + '/records').orderByChild('date').limitToLast(1).once('value', (dataSnapshot) => {
               obj.lastrecord = dataSnapshot.val()[Object.keys(dataSnapshot.val())[0]].value;
               obj.lastdate = Utils.getDate(dataSnapshot.val()[Object.keys(dataSnapshot.val())[0]].date);
               resolve1();
            });
          });
          let p2 = new Promise((resolve2,reject) => {
            window.firebase.database().ref(tables[type].tname + '/' + e + '/priceid').once('value', (dataSnapshot) => {
               obj.id = e;
               obj.price = that.chargeitems[dataSnapshot.val()].price;
               resolve2();
            });
          });
          Promise.all([p1, p2]).then(() => {
            temparray.push(obj);
            console.log(obj);
            resolve0();
          })
        });
        return getdata;

        });
        Promise.all(dataPromises).then(() => {
          console.log(temparray);
          if(type == 'water'){
            this.watermonitors =  temparray;
          }else{
            this.eletricmonitors =  temparray;
          }
         console.log(Date.now()-ts);
        });
      },
      generate () {
        const that = this;
        let elast=0,ecurrent=0,eprice=0,wlast=0,wcurrent=0,wprice=0;
        this.eletricmonitors.forEach((e) => {
          elast += Number(e.lastrecord);
          ecurrent += Number(e.current);
          eprice = e.price;
        });
         let str1 ='&ef=l'+elast+'c'+ecurrent+'p'+eprice;
        this.watermonitors.forEach((e) => {
          wlast += Number(e.lastrecord);
          wcurrent += Number(e.current);
          wprice = e.price;
        });
        let str2 ='&wf=l'+wlast+'c'+wcurrent+'p'+wprice;
          this.print =str2+str1;
          this.house.chargeitems.forEach((e) => {
          this.print = this.print +"&"+that.chargeitems[e].name+'='+that.chargeitems[e].price;
        });
        this.print=this.print+'&='+this.house.price+'&number='+this.house.number;
        this.billon = true;
      },
      handleSelect (item) {
        this[this.active] = false;
        this[item] = true;
        this.active = item;
        const that = this;
        if(this.active == 'history'){
          this.house.eletricmonitors.forEach((e) => {
            window.firebase.database().ref('eletricmonitors/' + e +'/records').once('value', (dataSnapshot) => {
              dataSnapshot.forEach((e) => {
                const obj = e.val();
                obj.label = Utils.getDate(obj.date);
                obj.parent = e.ref.parent.parent.key;
                that.historybills.push(obj);
              });

            });
          });

        }
      },
      handlebillselect (e) {
        console.log(e);
        window.firebase.database().ref('eletricmonitors/' + this.historybills[0].parent +'/records').once("value", function(snapshot) {
        let data = Object.keys(snapshot.val()).map((e) => {
          return snapshot.val()[e];
        });
        console.log(data);
            Utils.quicksort(data);

});
      },
  },
}
</script>

<style scoped>
.info-title{
  background-color: rgb(64, 158, 255);
  color: white;
  padding: 10px 15px;
}
.last-record-row{
  color: rgb(200,200,200);
  font-size: 15px;
  margin: 15px 0px;
}
.info-wrap{
  padding: 0px 15px;
}
.bill-title{
  padding: 15px 10px;
  background-color: #f5f7fa;
}
.link-btn-wrap{
  display: flex;
  justify-content: center;
  margin-top: 15px;
}
.link-button{
  text-decoration: none;
  display: inline-block;
  padding: 5px 20px;
  font-size: 15px;
  color: white;
  background-color: #049efe;
  border-radius: 5px;
}


</style>
